#calculator{
    top: 20%;
    right: 40%;
    .calculator-wrapper{
        width: 300px;
        .results-wrapper{
            margin-bottom: 20px;
            background-color: white;
            text-align: right;
            height: 50px;
            line-height: 50px;
            font-size: 24px;
            overflow: hidden;
            .results-container{
                padding-right: 10px;
                background-color: white;
                border-left-color: rgb(5, 6, 8) !important;
                border-top-color: rgb(5, 6, 8) !important;
                border-right-color: rgb(255, 255, 255) !important;
                border-bottom-color: rgb(255, 255, 255) !important;
                box-shadow: rgb(136, 140, 143) 1px 1px 0px 1px inset,
                  rgb(223, 224, 227) -1px -1px 0px 1px inset !important;
            }
        }
        .keypad-wrapper{
            width: 100%;
            .keypad-row{
                display: flex;
                justify-content: flex-end;
                height: 55px;
                line-height: 50px;
                .keypad{                    
                    border: 2px solid black;
                    background-color: rgb(206, 208, 207);
                    color: rgb(5, 6, 8);
                    border-left-color: rgb(255, 255, 255);
                    border-top-color: rgb(255, 255, 255);
                    border-right-color: rgb(5, 6, 8);
                    border-bottom-color: rgb(5, 6, 8);
                    box-shadow: rgb(223, 224, 227) 1px 1px 0px 1px inset,
                      rgb(136, 140, 143) -1px -1px 0px 1px inset;
                    width: 25%;
                    text-align: center;
                    font-size: 20px;
                    cursor: pointer;
                }
            }
        }
    }
}