.background-changer-wrapper {
  display: flex;
  width: 100%;
  flex-direction: column;
  text-align: center;
  .background-monitor {
    width: 50%;
    margin: 5% auto 30px;
    img {
      width: 100%;
    }
  }
  .background-list-wrapper {
    width: 55%;
    text-align: center;
    box-shadow: rgb(136, 140, 143) -1px -1px 0px 1px,
      rgb(136, 140, 143) -1px -1px 0px 1px inset;
    position: relative;
    user-select: none;
    padding: 15px;
    margin: 15px auto 60px;
    border-width: 1px;
    border-style: solid;
    border-color: white;
    border-image: initial;

    .list-title {
      position: absolute;
      top: -13px;
      left: 20px;
      padding: 0 7px;
      font-family: "Roboto Mono";
      font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen,
        Ubuntu, Cantarell, "Open Sans", "Helvetica Neue", sans-serif;
      background-color: #ced0cf;
    }
    .list-container {
      background-color: white;
      height: 100px;
      overflow: hidden scroll;
      text-align: left;
      ul {
        list-style: none;
        padding-left: 0px;
        margin: 0px;
        li {
          padding: 5px;
          font-size: 14px;
          padding-left: 10px;
        }
        .active {
          background-color: navy;
          border: 1px dotted white;
          color: white;
        }
      }
    }
    
  }
  .settings-cta-wrapper{
    display: flex;
    position: absolute;
    right: 20px;
    bottom: 20px;
    .win-btn{
      cursor: pointer;
      margin-right: 3px;
      display: flex;
      align-items: center;
      font-size: 14px;
      height: 30px;
      border: 2px solid black;
      padding: 0 0.5rem;
      background-color: rgb(206, 208, 207);
      color: rgb(5, 6, 8);
      border-left-color: rgb(255, 255, 255);
      border-top-color: rgb(255, 255, 255);
      border-right-color: rgb(5, 6, 8);
      border-bottom-color: rgb(5, 6, 8);
      box-shadow: rgb(223, 224, 227) 1px 1px 0px 1px inset,
        rgb(136, 140, 143) -1px -1px 0px 1px inset;
      
    }
    .win-btn:focus{
      outline: none;
    }
  }
  ::-webkit-scrollbar {
    width: 15px;
    border: 1px solid #ccc;
  }
  /* Track */
::-webkit-scrollbar-track {
  background: #f1f1f1; 
  // border: 1px solid #ccc;
}
 
/* Handle */
::-webkit-scrollbar-thumb {
  background: rgb(185, 185, 185);
  // border: 1px solid #ccc;
  
}

/* Handle on hover */
::-webkit-scrollbar-thumb:hover {
  background: rgb(206, 208, 207); 
  
}
}
