.taskbar-invert {
  background-color: rgb(206, 208, 207) !important;
  border-left-color: rgb(5, 6, 8) !important;
  border-top-color: rgb(5, 6, 8) !important;
  border-right-color: rgb(255, 255, 255) !important;
  border-bottom-color: rgb(255, 255, 255) !important;
  box-shadow: rgb(136, 140, 143) 1px 1px 0px 1px inset,
    rgb(223, 224, 227) -1px -1px 0px 1px inset !important;
}
.taskbarApp-invert {
  background-color: rgb(240, 240, 240) !important;
  border-left-color: rgb(5, 6, 8) !important;
  border-top-color: rgb(5, 6, 8) !important;
  border-right-color: rgb(255, 255, 255) !important;
  border-bottom-color: rgb(255, 255, 255) !important;
  box-shadow: rgb(136, 140, 143) 1px 1px 0px 1px inset,
    rgb(223, 224, 227) -1px -1px 0px 1px inset !important;
}
.notification-wrapper {
  position: absolute;
  right: 3px;
  width: 6%;
  text-align: right;
  margin-right: 3px !important;
  display: flex;
  justify-content: flex-end;
  min-width: 100px;
  .time {
    float: right;
    text-align: right;
  }
}
