// this files contains all the common styles that windows share - from border, to top bar and padding;
.window {
  position: absolute;
  // right: 0;
  width: fit-content;
  border-right: 1px solid black;
  border-bottom: 1px solid black;
  border-left: 1px solid white;
  background-color: rgb(206, 208, 207);
  .handle {
    height: 26px;
    background-color: rgb(0, 0, 128);
    display: flex;
    justify-content: space-between;
    align-items: center;
    border: 2px solid rgb(206, 208, 207);

    .window-info {
      margin-left: 5px;
      height: 15px;
      display: flex;
      color: white;
      font-size: 13px;
      align-items: center;
      img {
        height: 100%;
        margin-right: 5px;
      }
    }
    .window-controls {
      display: flex;
      align-items: center;
      // height: 17px;
      margin-right: 1px;
      .control {
        box-sizing: border-box;
        background-color: rgb(206, 208, 207);
        color: rgb(5, 6, 8);
        box-shadow: rgb(223, 224, 227) 1px 1px 0px 0px inset,
          rgb(136, 140, 143) -1px -1px 0px 1px inset;
        position: relative;
        display: inline-flex;
        -webkit-box-align: center;
        align-items: center;
        -webkit-box-pack: center;
        justify-content: center;
        width: 17px;
        height: 17px;
        top: -1px;
        font-size: 10px;
        font-weight: 900;
        margin-right: 3px;
        user-select: none;
        padding: 0px 0.5rem;
        border-width: initial;
        border-style: none;
        border-color: initial;
        border-image: initial;
        text-align: center;
      }
      .control:active {
        border-left-color: rgb(5, 6, 8);
        border-top-color: rgb(5, 6, 8);
        border-right-color: rgb(255, 255, 255);
        border-bottom-color: rgb(255, 255, 255);
        box-shadow: rgb(136, 140, 143) 1px 1px 0px 1px inset,
          rgb(223, 224, 227) -1px -1px 0px 1px inset;
      }
      .control:focus {
        border: none;
        outline: none;
      }
    }
  }
  .window-container {
    border: 4px solid rgb(206, 208, 207);
    border-top: none;
    .window-inner-container {
      padding: 15px 10px;
    }
  }
}
