.startMenu-wrapper {
  position: absolute;
  left: 5px;
  bottom: 0px;
  z-index: 9999;
  width: 200px;
  background-color: rgb(206, 208, 207);
  color: rgb(5, 6, 8);
  border-left-color: rgb(255, 255, 255);
  border-top-color: rgb(255, 255, 255);
  box-shadow: rgb(223, 224, 227) 1px 1px 15px 2px inset,
    rgb(136, 140, 143) -1px -1px 0px 1px inset;
  font-size: 16px;
  border-bottom-width: 2px;
  border-right-width: 1px;
  border-top-width: 1px;
  border-left-width: 1px;
  margin-bottom: 33px !important;
  border-style: solid;
  font-size: 14px;
  cursor: pointer;
  :hover {
    background-color: rgb(0, 0, 128);
    color: white;
  }
  .startMenu-item {
    padding-top: 15px;
    padding-bottom: 15px;
    padding-left: 10px;
    padding-right: 10px;
    display: flex;
    align-items: center;
    img {
      margin-right: 10px;
      width: 35px;
    }
    .item-name a{
      text-decoration: none;
      color: inherit;
    }
  }
  .startMenu-line {
    border-top: 2px solid rgb(146, 146, 146);
    border-bottom: 1px solid white;
  }
}
