:root {
  --blue: #007bff;
  --indigo: #6610f2;
  --purple: #6f42c1;
  --pink: #e83e8c;
  --red: #dc3545;
  --orange: #fd7e14;
  --yellow: #ffc107;
  --green: #28a745;
  --teal: #20c997;
  --cyan: #17a2b8;
  --white: #fff;
  --gray: #6c757d;
  --gray-dark: #343a40;
  --primary: #007bff;
  --secondary: #6c757d;
  --success: #28a745;
  --info: #17a2b8;
  --warning: #ffc107;
  --danger: #dc3545;
  --light: #f8f9fa;
  --dark: #343a40;
  --loading-bg: rgb(109, 127, 136);
  --win95-bg: #008080;
  --taskbar-bg: rgb(206, 208, 207);
}
* {
  box-sizing: border-box;
}
#root {
  min-width: 800px;
  width: 100%;
  height: 100vh;
  background-color: var(--loading-bg);
  font-family: "Roboto Mono", monospace;
  overflow: hidden;
}
