.loading-wrapper {
  height: 100vh;
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  .loading-container {
    text-align: center;
    width: 15%;
    img {
      width: 100%;
    }
    .hint-text {
      margin-top: 20px;
      color: white;
      animation: blink 1s infinite;
      opacity: 0;
      font-size: 12px;
    }
  }
}

@keyframes blink {
  0% {
    opacity: 0;
  }
  50% {
    opacity: 1;
  }
  100% {
    opacity: 0;
  }
}
