.taskbar-wrapper {
  display: flex;
  background-color: var(--taskbar-bg);
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
  //   height: 40px;
  padding-left: 5px;
  padding-right: 5px;
  padding-top: 3px;
  padding-bottom: 2px;
  .taskbar-bar {
    cursor: pointer;
    margin-right: 3px;
    display: flex;
    align-items: center;
    font-size: 14px;
    height: 30px;
    border: 2px solid black;
    padding: 0 0.5rem;
    background-color: rgb(206, 208, 207);
    color: rgb(5, 6, 8);
    border-left-color: rgb(255, 255, 255);
    border-top-color: rgb(255, 255, 255);
    border-right-color: rgb(5, 6, 8);
    border-bottom-color: rgb(5, 6, 8);
    box-shadow: rgb(223, 224, 227) 1px 1px 0px 1px inset,
      rgb(136, 140, 143) -1px -1px 0px 1px inset;

    img {
      margin-right: 8px;
      height: 19px;
    }
  }
  .start-wrapper {
    margin-right: 10px;
    width: fit-content;
  }
  .apptaskbar-wrapper {
    width: 13%;
    white-space: nowrap;
    overflow: hidden;
  }
}
