#browser {
  top: 10%;
  left: 25%;
  overflow: hidden;
  .loading-screen {
    // background-color: white;
    width: 99%;
    height: 82%;
    position: absolute;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    img {
      width: 40%;
    }
  }
  .window-inner-container {
    padding: 0px;
    .border-line {
      height: 1px;
      border-top: 2px solid #929292;
      border-bottom: 1px solid white;
    }
    .address-bar-wrapper {
      display: flex;
      font-size: 14px;
      margin-bottom: 10px;
      .url-container {
        padding-right: 10px;
        width: 100%;
        padding-left: 10px;
        background-color: white;
        border-left-color: rgb(5, 6, 8) !important;
        border-top-color: rgb(5, 6, 8) !important;
        border-right-color: rgb(255, 255, 255) !important;
        border-bottom-color: rgb(255, 255, 255) !important;
        box-shadow: rgb(136, 140, 143) 1px 1px 0px 1px inset,
          rgb(223, 224, 227) -1px -1px 0px 1px inset !important;
      }
    }
    .browser-top-wrapper {
      display: flex;
      margin-bottom: 5px;
      margin-top: 5px;
      .browser-icons-wrapper {
        display: flex;
        // margin-right: 10px;
        padding-right: 10px;
        text-align: center;
        border-right: 2px solid #929292;
        border-left: 1px solid white;
        .browser-icon {
          margin-left: 10px;
          margin-right: 10px;
          text-align: center;
          .browser-img img {
            width: 20px;
          }
        }
        .browser-name {
          font-size: 10px;
        }
      }
    }
    .browser-window-container {
      // background-color: white;
      position: absolute;
      overflow: hidden scroll;
      width: 99%;
      height: 82%;
      .reddit-post-container {
        padding: 20px 10px;
        width: 100%;
        background-color: white;
        border-left-color: rgb(5, 6, 8) !important;
        border-top-color: rgb(5, 6, 8) !important;
        border-right-color: rgb(255, 255, 255) !important;
        border-bottom-color: rgb(255, 255, 255) !important;
        box-shadow: rgb(136, 140, 143) 1px 1px 0px 1px inset,
          rgb(223, 224, 227) -1px -1px 0px 1px inset !important;
        .author {
          font-size: 12px;
          margin-bottom: 5px;
        }
        .title {
          font-size: 14px;
          margin-bottom: 10px;
          font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto,
            Oxygen, Ubuntu, Cantarell, "Open Sans", "Helvetica Neue", sans-serif;
        }
        .post-info {
          display: flex;
          font-size: 12px;
          div {
            margin-right: 10px;
          }
        }
      }
    }
  }
}
